"use client";

import { useState, useEffect } from 'react';
import Image from 'next/image';
import Header from '@/components/Header';
import Footer from '@/components/Footer';
import { Product, Quote, QuoteItem, Customer } from '@/lib/database';
import { generateQuotePDF, QuotePDFData } from '@/lib/pdfGenerator';

// Types and Interfaces
interface CartItem {
  product: Product;
  quantity: number;
  unitPrice: number;
  discount?: number;
  margin?: number;
  customName?: string;
  salesPrice?: number;
  manualSalesPrice?: boolean;
  isGrouped?: boolean;
  groupName?: string;
  groupItems?: CartItem[];
}

export default function Home() {
  // States
  const [products, setProducts] = useState<Product[]>([]);
  const [customers, setCustomers] = useState<Customer[]>([]);
  const [cart, setCart] = useState<CartItem[]>([]);
  const [selectedProducts, setSelectedProducts] = useState<number[]>([]);
  const [isGrouping, setIsGrouping] = useState(false);
  const [groupName, setGroupName] = useState('');
  const [customerInfo, setCustomerInfo] = useState({
    name: '',
    email: '',
    phone: '',
    company: '',
    address: ''
  });
  const [projectDetails, setProjectDetails] = useState({
    projectDesign: '',
    projectDescription: ''
  });
  const [schedule, setSchedule] = useState({
    startDate: '',
    endDate: ''
  });
  const [conditions, setConditions] = useState({
    validityPeriod: '30',
    deliveryTime: ''
  });
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('');
  const [selectedCustomer, setSelectedCustomer] = useState('');
  const [activeStep, setActiveStep] = useState(1);
  const [editingQuoteId, setEditingQuoteId] = useState<number | null>(null);
  const [width, setWidth] = useState<number>(0);
  const [height, setHeight] = useState<number>(0);
  const [count, setCount] = useState<number>(1);
  const [squareMeters, setSquareMeters] = useState<number>(0);

  // Effects
  useEffect(() => {
    setSquareMeters(width * height * count);
  }, [width, height, count]);

  useEffect(() => {
    fetchProducts();
    fetchCustomers();
    
    // Check if editing a quote
    const urlParams = new URLSearchParams(window.location.search);
    const isEditing = urlParams.get('edit') === 'true';
    
    if (isEditing) {
      const editQuoteData = sessionStorage.getItem('editQuote');
      if (editQuoteData) {
        const quoteData = JSON.parse(editQuoteData);
        loadQuoteForEditing(quoteData);
        sessionStorage.removeItem('editQuote'); // Clean up
      }
    }
  }, []);

  // API Functions
  const fetchProducts = async () => {
    try {
      const response = await fetch('/api/products');
      const data = await response.json();
      setProducts(data);
    } catch (error) {
      console.error('√úr√ºnler y√ºklenirken hata:', error);
    } finally {
      setLoading(false);
    }
  };

  const fetchCustomers = async () => {
    try {
      const response = await fetch('/api/customers');
      const data = await response.json();
      setCustomers(data);
    } catch (error) {
      console.error('M√º≈üteriler y√ºklenirken hata:', error);
    }
  };

  // Quote Functions
  const loadQuoteForEditing = async (quoteData: any) => {
    try {
      setEditingQuoteId(quoteData.id);
      
      // Load customer info
      setCustomerInfo({
        name: quoteData.customer_name || '',
        email: quoteData.customer_email || '',
        phone: quoteData.customer_phone || '',
        company: quoteData.company || '',
        address: ''
      });
      
      // Parse notes for project details
      const notes = quoteData.notes || '';
      const projectDesign = notes.match(/Proje Tasarƒ±m: ([^\n]*)/)?.[1] || '';
      const projectDescription = notes.match(/Proje A√ßƒ±klama: ([^\n]*)/)?.[1] || '';
      const startDate = notes.match(/Ba≈ülangƒ±√ß: ([^\n]*)/)?.[1] || '';
      const endDate = notes.match(/Biti≈ü: ([^\n]*)/)?.[1] || '';
      const validityPeriod = notes.match(/Ge√ßerlilik: ([^\n]*)/)?.[1] || '30';
      const deliveryTime = notes.match(/Teslim: ([^\n]*)/)?.[1] || '';
      
      setProjectDetails({
        projectDesign,
        projectDescription
      });
      
      setSchedule({
        startDate,
        endDate
      });
      
      setConditions({
        validityPeriod: validityPeriod.replace(' g√ºn', ''),
        deliveryTime
      });
      
      // Load quote items
      const response = await fetch(`/api/quotes/${quoteData.id}`);
      if (response.ok) {
        const details = await response.json();
        const cartItems: CartItem[] = details.items?.map((item: any) => ({
          product: {
            id: item.product_id,
            name: item.product_name || 'Unknown Product',
            brand: item.brand || '',
            model: item.model || '',
            code: item.code || '',
            category: '',
            price: item.unit_price,
            description: '',
            specifications: '',
            stock_quantity: 0,
            unit: 'adet'
          },
          quantity: item.quantity,
          unitPrice: item.unit_price
        })) || [];
        
        setCart(cartItems);
      }
    } catch (error) {
      console.error('Teklif y√ºklenirken hata:', error);
    }
  };

  // Group Functions
  const handleGroupProducts = () => {
    if (selectedProducts.length < 2) {
      alert('En az 2 √ºr√ºn se√ßmelisiniz');
      return;
    }
    setIsGrouping(true);
  };

  const createGroup = () => {
    if (!groupName) {
      alert('Grup adƒ± giriniz');
      return;
    }

    const selectedItems = cart.filter(item => selectedProducts.includes(item.product.id));
    const totalPrice = selectedItems.reduce((sum, item) => sum + (item.unitPrice * item.quantity), 0);
    
    const groupProduct: Product = {
      id: Date.now(),
      name: groupName,
      price: totalPrice,
      category: 'Grup',
      stock_quantity: 1,
      brand: '',
      model: '',
      code: '',
      description: '',
      specifications: '',
      unit: 'grup'
    };

    const groupHeader: CartItem = {
      product: groupProduct,
      quantity: 1,
      unitPrice: totalPrice,
      isGrouped: true,
      groupItems: selectedItems
    };

    setCart(prev => [
      ...prev.filter(item => !selectedProducts.includes(item.product.id)),
      groupHeader
    ]);

    setSelectedProducts([]);
    setIsGrouping(false);
    setGroupName('');
  };

  // Cart Functions
  const addToCart = (product: Product) => {
    const price = product.price || 0;
    setCart(prev => {
      const existingItem = prev.find(item => item.product.id === product.id);
      if (existingItem) {
        return prev.map(item =>
          item.product.id === product.id
            ? { ...item, quantity: item.quantity + 1 }
            : item
        );
      }
      const newItem: CartItem = {
        product,
        quantity: 1,
        unitPrice: price
      };
      return [...prev, newItem];
    });
  };

  const removeFromCart = (productId: number) => {
    setCart(prev => prev.filter(item => item.product.id !== productId));
    setSelectedProducts(prev => prev.filter(id => id !== productId));
  };

  const updateQuantity = (productId: number, quantity: number) => {
    if (quantity < 1) return;
    setCart(prev =>
      prev.map(item =>
        item.product.id === productId
          ? { ...item, quantity }
          : item
      )
    );
  };

  const updateUnitPrice = (productId: number, price: number) => {
    setCart(prev =>
      prev.map(item =>
        item.product.id === productId
          ? { ...item, unitPrice: price, manualSalesPrice: true }
          : item
      )
    );
  };

  // Checkbox Functions
  const toggleProductSelection = (productId: number) => {
    setSelectedProducts(prev =>
      prev.includes(productId)
        ? prev.filter(id => id !== productId)
        : [...prev, productId]
    );
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100">
      <Header />
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8 relative z-10">
        <div className="bg-white/90 backdrop-blur-lg rounded-3xl shadow-2xl overflow-hidden border border-white/20 hover:shadow-3xl transition-all duration-500">
          <div className="p-8 md:p-12">
            {/* Kategorile≈ütirme Modalƒ± */}
            {isGrouping && (
              <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
                <div className="bg-white rounded-2xl p-8 max-w-md w-full mx-4">
                  <h3 className="text-2xl font-bold mb-4">Kategori Adƒ± Girin</h3>
                  <input
                    type="text"
                    value={groupName}
                    onChange={(e) => setGroupName(e.target.value)}
                    className="w-full px-4 py-3 border border-gray-300 rounded-xl mb-4"
                    placeholder="√ñrn: Soƒüutma Grubu"
                  />
                  <div className="flex justify-end space-x-3">
                    <button
                      onClick={() => setIsGrouping(false)}
                      className="px-4 py-2 text-gray-600 hover:text-gray-800"
                    >
                      ƒ∞ptal
                    </button>
                    <button
                      onClick={createGroup}
                      className="px-4 py-2 bg-purple-600 text-white rounded-lg hover:bg-purple-700"
                    >
                      Olu≈ütur
                    </button>
                  </div>
                </div>
              </div>
            )}

            {/* Sepet */}
            {cart.length > 0 && (
              <div className="mb-8">
                <div className="flex justify-between items-center mb-4">
                  <h2 className="text-2xl font-bold">Sepet</h2>
                  {selectedProducts.length > 1 && (
                    <button
                      onClick={handleGroupProducts}
                      className="px-4 py-2 bg-purple-600 text-white rounded-lg hover:bg-purple-700"
                    >
                      Kategorile≈ütir
                    </button>
                  )}
                </div>
                
                <div className="overflow-x-auto">
                  <table className="min-w-full divide-y divide-gray-200">
                    <thead className="bg-gray-50">
                      <tr>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          Se√ß
                        </th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          √úr√ºn
                        </th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          Adet
                        </th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          Birim Fiyat
                        </th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          Toplam
                        </th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          ƒ∞≈ülemler
                        </th>
                      </tr>
                    </thead>
                    <tbody className="bg-white divide-y divide-gray-200">
                      {cart.map((item) => (
                        <tr key={item.product.id}>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <input
                              type="checkbox"
                              checked={selectedProducts.includes(item.product.id)}
                              onChange={() => toggleProductSelection(item.product.id)}
                              className="h-4 w-4 text-purple-600 focus:ring-purple-500 border-gray-300 rounded"
                            />
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <span className="text-sm font-medium text-gray-900">
                              {item.product.name}
                            </span>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <input
                              type="number"
                              value={item.quantity}
                              onChange={(e) => updateQuantity(item.product.id, parseInt(e.target.value))}
                              className="w-20 px-2 py-1 border border-gray-300 rounded"
                              min="1"
                            />
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <input
                              type="number"
                              value={item.unitPrice}
                              onChange={(e) => updateUnitPrice(item.product.id, parseFloat(e.target.value))}
                              className="w-32 px-2 py-1 border border-gray-300 rounded"
                              step="0.01"
                              min="0"
                            />
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <span className="text-sm text-gray-900">
                              {(item.quantity * item.unitPrice).toFixed(2)} TL
                            </span>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                            <button
                              onClick={() => removeFromCart(item.product.id)}
                              className="text-red-600 hover:text-red-900"
                            >
                              Sil
                            </button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </div>
            )}

            {/* √úr√ºn Listesi */}
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
              {products.map((product) => (
                <div
                  key={product.id}
                  className="bg-white rounded-xl shadow-lg p-6 hover:shadow-xl transition-shadow duration-300"
                >
                  <h3 className="text-lg font-semibold mb-2">{product.name}</h3>
                  <p className="text-gray-600 mb-4">{product.category}</p>
                  <p className="text-lg font-bold text-purple-600 mb-4">
                    {(product.price || 0).toFixed(2)} TL
                  </p>
                  <button
                    onClick={() => addToCart(product)}
                    className="w-full bg-purple-600 text-white py-2 px-4 rounded-lg hover:bg-purple-700 transition-colors duration-300"
                  >
                    Sepete Ekle
                  </button>
                </div>
              ))}
            </div>
          </div>
        </div>
      </main>
      <Footer />
    </div>
  );
}